import { getDataJenis } from "../../../actions/datamaster_action";
import {
  React,
  connect,
  reduxForm,
  Field,
  ReanderSelect,
  ReanderField,
  ToastNotification,
  Loading
} from "../../../components/helpers/library";
import { postDataNoTImeOut2 } from "../../../config/axios";
import ValidasiStockOpname from "../../../Validasi/ValidasiStockOpname";

const maptostate = (state) => {
  return {
    datajenis: state.datamaster.getDataJenis,
  };
};
class HeadStockOpnamePermintaan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      settings: "",
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }

  componentDidMount() {
    this.props.dispatch(getDataJenis());
    this.props.change("kode_dept", "ALL");
  }
  cariData(e) {
    this.setState({
      isLoading: true,
    });
    postDataNoTImeOut2("penerimaan-suplier/ready-input?kode_dept=" + e)
      .then((res) => {
        //   console.log(res);
        this.setState({
          isLoading: false,
        });
        ToastNotification("success", res.data.message || "Data Ditemukan");
        this.props.change("berat_awaal", res.data.saldo);
      })
      .catch((err) => {
        this.setState({
          isLoading: false,
        });
        ToastNotification(
          "info",
          err.response?.data?.message || "Data Tidak Ditermukan"
        );
      });
  }
  render() {
    let datajenis = [];
    this.props.datajenis.forEach((element) => {
      let row = {
        value: element.kode_dept,
        name: element.kode_dept,
      };
      datajenis.push(row);
    });

    return (
      <>
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
            <div className="col-3">
              <Field
                name="kode_dept"
                label="Kode Jenis"
                placeholder="Pilih Kode Jenis"
                options={datajenis}
                onChange={(e) => this.cariData(e)}
                component={ReanderSelect}
              />
            </div>
            <div className="col-3">
              <Field
                name="berat_awaal"
                label="Berat Awal"
                placeholder="Masukan Berat Awal"
                readOnly
                onFocus={(e) =>
                  this.props.change("berat_opname", e.target.value)
                }
                component={ReanderField}
              />
            </div>
            <div className="col-3">
              <Field
                name="berat_opname"
                label="Berat Opname"
                placeholder="Masukan Berat Opname"
                component={ReanderField}
              />
            </div>
            <div className="col-3">
              <button className="btn btn-primary btn-block mt-4" type="submit">
                {this.state.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Simpan
                  </>
                ) : (
                  "Simpan"
                )}
              </button>
            </div>
          </div>
        </form>
        {this.state.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </>
    );
  }
}

HeadStockOpnamePermintaan = reduxForm({
  form: "HeadStockOpnamePermintaan",
  enableReinitialize: true,
  validate : ValidasiStockOpname
})(HeadStockOpnamePermintaan);
export default connect(maptostate)(HeadStockOpnamePermintaan);
