const ValidasiStockOpname = (value, event) => {
    const errors = {};
    // console.log(event.charCode==13);
    if (!value.berat_opname) {
      errors.berat_opname = "Berat Opname Tidak Boleh Kosong";
    }
    if (!value.berat_awaal) {
      errors.berat_awaal = "Berat Awal Tidak Boleh Kosong";
    }
    if (!value.kode_dept) {
      errors.kode_dept = "Kode Dept Harus Dipilih";
    }
    return errors;
  };
  
  export default ValidasiStockOpname;
  