import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ToastNotification,
  reset,
} from "../../../components/helpers/library";
import { postDataNoTImeOut2 } from "../../../config/axios";
import HeadStockOpnamePermintaan from "./HeadStockOpnamePermintaan";

class StockOpnamePenerimaan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      settings: "",
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    let hasil = {
      kode_dept: data.kode_dept,
      berat_opname: parseFloat(data.berat_opname),
      berat_awal: parseFloat(data.berat_awaal),
    };
    postDataNoTImeOut2("penerimaan-suplier/opnames", hasil).then((res) => {
        ToastNotification("success",res.data.message || "Berhasil Tersimpan")
        this.props.dispatch(reset('HeadStockOpnamePermintaan'))
    }).catch((err)=>{
        console.log(err);
        ToastNotification('info',err.response?.data?.message || "Gagal Tersimpan")
    })
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Stock Opname</Link>
          </li>
          <li className="breadcrumb-item active">Stock Opname Penerimaaan</li>
        </ol>
        <h1 className="page-header">Stock Opname Penerimaaan </h1>
        <Panel>
          <PanelHeader>Stock Opname Penerimaaan</PanelHeader>
          <PanelBody>
            <HeadStockOpnamePermintaan
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

StockOpnamePenerimaan = reduxForm({
  form: "StockOpnamePenerimaan",
  enableReinitialize: true,
})(StockOpnamePenerimaan);
export default connect(null)(StockOpnamePenerimaan);
